//product category / shop
.woocommerce,
.woocommerce-page {

    .woocommerce-products-header__title {
        color: $color__site-red-light;
    }

    .woocommerce-LoopProduct-link img {
        border-bottom: 1px solid #e2e2e2;
        padding: .5em;
    }

    ul.products {

        //remove stock on shop pages
        p.stock {
            display: none;
        }

        li.product {
            border: 2px solid transparent;
            box-shadow: 0 2px 6px 1px rgba(0,0,0,.1);

            .woocommerce-loop-category__title,
            .woocommerce-loop-product__title {
                padding: .5em;
                font-size: 1rem;
            }

            .button {
                margin: 1em auto;
                font-size: 1rem;
                display: block;
                width: 75%;
                text-align: center;
                background: $color__site-red-light;
                color: #fff;
            }

            .amount {
                padding: .5em;
                font-size: 1rem;
            }

            //kill view cart
            .added_to_cart.wc-forward {
                display: none;
            }

            //underline product img on hover
            .woocommerce-loop-product__link {

                img {
                    transition: all .2s linear;
                }

                &:hover {

                    img {
                        border-bottom: 1px solid $color__site-red-deep;
                    }
                }
            }
        }
    }

    div.product div.images .woocommerce-product-gallery__trigger {
        z-index: 2;
    }

    span.onsale {
        z-index: 2;
    }
}

//single product 
.single-product {
    font-family: $font__sans;

    .product_title {
        font-size: 1.5rem;
    }

    div.product {

        p.price {
            font-size: 1.05em;
        }

        form.cart {
            display: flex;
            align-items: center;

            .stock {
                display: none;
            } 
        }

    }

    //image size
    #content div.product div.images {
        width: 36%;
        margin-right: 1em;

        ol.flex-control-nav {
            display: flex;
            justify-content: space-evenly;
            margin-top: .25em;
        }
    }

    //image summary size
    #content div.product div.summary {
        float: none;
        width: 100%;
    }


    .woocommerce-product-details__short-description {
        font-size: 1rem;

        ul {
            list-style: none;

            li {
                margin-bottom: .25em;
            }
        }
    }

    .entry-summary {
        padding: 1em 2em 0;
        margin-top: 0;
    }

    .wc-tabs-wrapper {

        .woocommerce-Tabs-panel--description,
        .woocommerce-Tabs-panel--reviews {
            font-size: 1rem;

            h2,
            h3,
            h4,
            h5 {
                margin-top: 0;
                font-size: 1.5rem;
            }

            strong,
            p, {
                font-weight: normal;
                font-size: 1rem;
            }
        }
    }

    .related,
    .upsells {

        h2 {
            font-size: 1.5rem;
        }
    }
}

//change color add to cart
.woocommerce div.product form.cart {

    .button {
        background: $color__site-red-light;
        color: #fff;
    }

    .added_to_cart {
        display: none;
    }
}

//price filter color
.woocommerce .widget_price_filter .price_slider_wrapper .ui-widget-content {
    background-color: $color__site-red-deep;
}

.woocommerce .widget_price_filter .ui-slider .ui-slider-range {
    background-color: $color__site-red-deep;
}

.woocommerce .widget_price_filter .ui-slider .ui-slider-handle {
    background-color: $color__site-red-deep;
}

//social share
.social-share-wrapper {
    display: none;
}

.single-product {

    .social-share-wrapper {
        display: flex;
        align-items: center;

        div {
            margin-right: .25em;
        }

        .share-on {
            font-size: 1rem;
        }

        .facebook,
        .twitter,
        .pinterest,
        .googleplus {
            font-size: .75rem;
            padding: .2em .45em .2em .2em;
            border-radius: 3px;
        }

        .facebook {
            background: #3B5998;

            a {
                color: #fff;
            }

            i {
                padding: 0 .25em;
            }
        }

        .twitter {
            background: #1DA1F2;

            a {
                color: #fff;
            }

            i {
                padding: 0 .25em;
            }
        }

        .pinterest {
            background: #BD081C;

            a {
                color: #fff;
            }

            i {
                padding: 0 .25em;
            }
        }

        .googleplus {
            background: #DB4437;

            a {
                color: #fff;
            }

            i {
                padding: 0 .25em;
            }
        }
    }
}